/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { injectIntl } from 'react-intl';
import { bindActionCreators } from 'redux';
import { Layout, Dropdown, Icon, Button, Tag, Menu, Spin, message, Row, Col } from 'antd';
import { Switch, Route, Redirect, Link, withRouter } from 'react-router-dom';
import axios from 'axios';

import {
  PrivateAdminRoute,
  DashboardCmp,
  ProcessServices,
  Profile,
  Configuration,
  Nodes,
  Models,
  Users,
  Menus,
  Eds,
  Services,
  Engines,
  Stats,
  NotFound,
  PayCancel,
  PayError,
  PaySuccess,
} from './routes';
import { Logo, BadgeWrapper, LayoutWrapper, DivHeader, MenuItem } from './styles';
import auth from './../../utils/auth';
import functions from './../../utils/functions';
import { config } from './../../config';

import { changeLang } from './../../reducers/auth';
import { getUser, clearNotification, enginesByUserv1 } from './../../services';
import * as notificationActions from './../../reducers/notification';
import * as processActions from './../../reducers/process';
import * as authActions from './../../reducers/auth';

import { HeadCmp } from 'components/HeadCmp';
import './index.css';

const { Header, Content, Footer, Sider } = Layout;

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

const Dashboard = props => {
  const {
    location: { pathname },
    process,
    authState,
    intl: { messages },
  } = props;

  const countN = props.notificationState.notifications
    ? props.notificationState.notifications.length
    : 0;
  const [allowedFiles, setAllowedFiles] = useState('');
  const [collapsed, setCollapsed] = useState(false);
  const [notifications, setNotifications] = useState(props.notificationState.notifications);
  const [loading, setLoading] = useState(false);
  const [defaultValue, setDefaultValue] = useState(0);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();

    _getUser(source);
    _getEngines(source);

    return function cleanup() {
      source.cancel('Operation canceled by the user.');
    };
  }, []);

  const _getUser = async signal => {
    const {
      history,
      authAct: { fetchUser },
      notificationAct: { changeNotifications },
    } = props;
    setLoading(true);
    try {
      let result = await getUser(signal);
      fetchUser(result.data.user);
      changeNotifications(result.data.user.Notifications);
      setLoading(false);
    } catch (err) {
      auth.clearAppStorage();
      history.push('/');
      setLoading(false);
    }
  };

  const clearNotifications = async () => {
    try {
      await clearNotification();
      setNotifications([]);
    } catch (error) {
      message.error(error.message);
    }
  };

  const _getEngines = signal => {
    const {
      processAct: { getEngines, fetchRequest, fetchResponse },
    } = props;

    fetchRequest();
    enginesByUserv1(signal)
      .then(response => {
        let data = response.data;
        if (data.process && data.allowedFiles) {
          getEngines(data.process, data.process[0].name);
          setDefaultValue(jsUcfirst('translation'));
          setAllowedFiles(data.allowedFiles);
          fetchResponse();
        }
      })
      .catch(error => {
        fetchResponse();
      });
  };

  const onCollapse = () => {
    setCollapsed(!collapsed);
  };

  const logout = () => {
    const { history } = props;
    auth.clearAppStorage();
    history.push('/');
  };

  const _selectProcess = type => {
    const {
      processAct: { selectProcess },
    } = props;
    selectProcess(type);
  };

  const _changeFileID = fileId => {
    const {
      processAct: { changeFileID },
    } = props;
    changeFileID(fileId);
  };

  const menu = (
    <Menu>
      <Menu.Item key="2">
        <Link to="/dashboard/profile">
          <Icon type="profile" /> &nbsp;&nbsp;{messages.menus.profile}
        </Link>
      </Menu.Item>
      <Menu.Item key="3" onClick={logout}>
        <Icon type="logout" /> {messages.menus.logout}
      </Menu.Item>
    </Menu>
  );

  const menuLang = (
    <Menu>
      <Menu.Item
        key="en"
        disabled={props.lang === 'en'}
        onClick={() => {
          props.authChangeLang('en');
        }}
      >
        <span role="img" aria-label="English">
          🇺🇸
        </span>
        {messages.languages.en}
      </Menu.Item>
      <Menu.Item
        key="es"
        disabled={props.lang === 'es'}
        onClick={() => {
          props.authChangeLang('es');
        }}
      >
        <span role="img" aria-label="Spanish">
          🇪🇸
        </span>
        {messages.languages.es}
      </Menu.Item>
    </Menu>
  );

  const aux = notifications.map((item, index) => {
    let status = functions.getStatusByText(item.type);
    return (
      <Menu.Item key={`noty${index}`}>
        <Icon type="desktop" /> {item.data.fileName}{' '}
        <Tag style={{ float: 'right', margin: 2, marginLeft: 5 }} color="#2db7f5">
          {status}
        </Tag>
      </Menu.Item>
    );
  });

  let array = [];
  let tmp = 0;
  for (let index = aux.length - 1; index >= 0; index--) {
    array.push(aux[index]);
    tmp++;

    if (tmp === 5) break;
  }
  array.push(
    <MenuItem key={`more`}>
      <React.Fragment>
        <Button
          onClick={clearNotifications}
          style={{ float: 'left', marginBottom: 5 }}
          size="small"
          type="primary"
        >
          Clear
        </Button>
        <Link to="/dashboard/process-services">{messages.default.seeMore}...</Link>
      </React.Fragment>
    </MenuItem>
  );

  // const menuNoty = <Menu>{array}</Menu>;

  if (loading) {
    return (
      <Row justify="center" type="flex">
        <HeadCmp />
        <Col
          xs={12}
          style={{
            display: 'flex',
            justifyContent: 'center',
            marginTop: 60,
          }}
        >
          <Spin size="large" spinning={loading} tip={messages.default.loading + '...'} />
        </Col>
      </Row>
    );
  } else {
    return (
      <LayoutWrapper>
        <HeadCmp />
        <Sider width={256} collapsed={collapsed}>
          <Logo>
            {collapsed ? (
              <img alt="logo" src={config[config.default].logoMin} />
            ) : (
              <img alt="logo" src={config[config.default].logo} />
            )}
          </Logo>
          <Menus pathname={pathname} rol={authState.user.rol} />
        </Sider>
        <Layout>
          <Header
            style={{
              background: '#fff',
              padding: 0,
            }}
          >
            <Icon
              className="trigger"
              type={collapsed ? 'menu-unfold' : 'menu-fold'}
              onClick={onCollapse}
            />
            <DivHeader>
              {authState.user.rol !== 'user' ||
                (countN === 0 && (
                  <BadgeWrapper count={0}>
                    <Button
                      disabled={countN === 0}
                      style={{ marginLeft: 10 }}
                      type="primary"
                      shape="circle"
                      icon="notification"
                    />
                  </BadgeWrapper>
                ))}
              <Dropdown overlay={menuLang} placement="bottomRight">
                <Button style={{ marginLeft: 10 }} type="primary" shape="circle" icon="global" />
              </Dropdown>
              <Dropdown overlay={menu} placement="bottomRight">
                {/* <AvatarWrapper type="primary" style={{ marginLeft: 10 }} icon="user" /> */}
                <Button style={{ marginLeft: 10 }} type="primary" icon="user">
                  {authState.user && authState.user.fullName ? authState.user.fullName : ''}
                  {}
                </Button>
              </Dropdown>{' '}
            </DivHeader>
          </Header>{' '}
          <Content
            style={{
              margin: '0 16px',
            }}
          >
            <Switch>
              <Route
                exact
                path="/dashboard"
                render={() => <DashboardCmp user={authState.user} />}
              />
              <Route
                exact
                path="/dashboard/users"
                render={() => <Users userLogin={authState.user} />}
              />
              <Route
                exact
                path="/dashboard/users/:rol/:id"
                render={() => <DashboardCmp userLogin={authState.user} />}
              />
              <Route
                exact
                path="/dashboard/process-services"
                render={() => (
                  <ProcessServices
                    userLogin={authState.user}
                    typeOfUser={authState.user.typeOfUser}
                    changeFileID={_changeFileID}
                    process={process}
                    allowedFiles={allowedFiles}
                    defaultValue={defaultValue}
                    selectProcess={_selectProcess}
                    newProcess={false}
                  />
                )}
              />
              <Route
                exact
                path="/dashboard/process-services/new"
                render={() => (
                  <ProcessServices
                    userLogin={authState.user}
                    typeOfUser={authState.user.typeOfUser}
                    changeFileID={_changeFileID}
                    process={process}
                    allowedFiles={allowedFiles}
                    defaultValue={defaultValue}
                    selectProcess={_selectProcess}
                    newProcess={true}
                  />
                )}
              />

              <Route
                exact
                path="/dashboard/process-services/:uuid/success"
                render={() => <PaySuccess dashboard />}
              />
              <Route
                exact
                path="/dashboard/process-services/:uuid/cancel"
                render={() => <PayCancel dashboard />}
              />
              <Route
                exact
                path="/dashboard/process-services/:uuid/error"
                render={() => <PayError dashboard />}
              />

              <Route exact path="/dashboard/profile" component={Profile} />

              <Route
                exact
                path="/dashboard/stats/:userId"
                render={() => <Stats user={authState.user} />}
              />
              <Route exact path="/dashboard/stats" render={() => <Stats user={authState.user} />} />

              <PrivateAdminRoute
                exact
                path="/dashboard/configuration"
                user={authState.user}
                component={Configuration}
              />

              <PrivateAdminRoute
                exact
                path="/dashboard/services"
                user={authState.user}
                component={Services}
              />
              <PrivateAdminRoute
                exact
                path="/dashboard/nodes"
                user={authState.user}
                component={Nodes}
              />

              <PrivateAdminRoute
                exact
                path="/dashboard/engines"
                user={authState.user}
                client={true}
                component={Engines}
              />
              <PrivateAdminRoute
                exact
                client={true}
                path="/dashboard/engines/user/:userId"
                user={authState.user}
                component={Engines}
              />
              <PrivateAdminRoute
                exact
                user={authState.user}
                client={true}
                path="/dashboard/models"
                component={Models}
              />
              <PrivateAdminRoute
                exact
                path="/dashboard/eds"
                user={authState.user}
                component={Eds}
              />
              <PrivateAdminRoute
                exact
                path="/dashboard/eds/:filter/:id"
                user={authState.user}
                component={Eds}
              />

              <Route exact path="/dashboard/404" render={() => <NotFound dashboard />} />
              <Redirect to="/dashboard/404" />
            </Switch>
          </Content>
          <Footer
            style={{
              textAlign: 'center',
            }}
          >
            {config[config.default].footer}
          </Footer>
        </Layout>
      </LayoutWrapper>
    );
  }
};

function mapDispatchToProps(dispatch) {
  return {
    processAct: bindActionCreators(processActions, dispatch),
    notificationAct: bindActionCreators(notificationActions, dispatch),
    authAct: bindActionCreators(authActions, dispatch),
    authChangeLang: bindActionCreators(changeLang, dispatch),
  };
}

function mapStateToProps(state) {
  return {
    process: state.rootReducer.process,
    authState: state.rootReducer.auth,
    notificationState: state.rootReducer.notification,
    location: state.router.location,
    lang: state.rootReducer.auth.lang,
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(injectIntl(Dashboard)));
